body {
    background-color: #FAFBFB;
}

h3 {
    text-align: center;
}

.wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.wrapper, .top {
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

}
.top {
    margin-top: 14px;
    top: 15px;
}

.bottom {
    top: 40px;
}

.top, .bottom {
    display: inherit;
    position: relative;
}

.card > .card-content {
    padding: 14px 0 5px 22px;    
}

.boxOne, .boxTwo, .boxThree, .boxFour {
    padding: 0 10px 0 10px;
    margin: 0 15px 0 10px;
    opacity: 1;        
}

.material-icons.right {
    margin-right: 15px;
    font-size: 25px;
}

.material-icons.right:hover {
    opacity: .6;
}

.activator {
    padding: 10px 10px 0 10px;
}

.card-title.grey-text.text-darken-4.activator, p, h3 {
    font-family: 'Raleway', sans-serif;
}

p {
    margin-top: 40px;
}

.fa.fa-github {
    font-size: 40px;
}

.fa.fa-external-link {
    font-size: 35px;
    margin-right: 10px;
    margin-left: 5px;
}

.fa.fa-github:hover, .fa.fa-external-link:hover {
    opacity: .6;
}



